import React from 'react';
import * as yup from 'yup';
import { useFormik } from 'formik';

export default ({ setContacts, setSuccess }) => {
  const onSendBtn = () => {
    setSuccess(true);
    setContacts(false);
    resetForm();
  };

  const validationSchema = yup.object().shape({
    name: yup.string(),
    telegram: yup.string().min(2, 'Lorem ipsum').required('  '),
    email: yup.string().email('Lorem ipsum').required('  '),
    comment: yup.string().min(5, 'Lorem ipsum').required('  '),
  });

  const { values, errors, touched, resetForm, handleBlur, handleSubmit, handleChange } = useFormik({
    initialValues: {
      name: '',
      email: '',
      telegram: '',
      comment: '',
    },
    validationSchema,
    onSubmit: onSendBtn,
  });

  return (
    <React.Fragment>
      <div id="modal">
        <div className="modal_wrap">
          <div className="modal contact_modal">
            <div className="title_block">
              <h6>Contact us</h6>
              <div className="close" onClick={() => setContacts(false)}></div>
            </div>
            <div className="scroll_modal">
              <form className="contact_form" onSubmit={handleSubmit}>
                <label className="input_label">
                  <input
                    type="text"
                    name="name"
                    className="input"
                    placeholder=" "
                    value={values.name}
                    onChange={handleChange}
                  />
                  <span className="placeholder">
                    <span className="prev_plh"></span>
                    <span className="text_plh">Name</span>
                    <span className="last_plh"></span>
                  </span>
                </label>
                <label className="input_label">
                  <input
                    type="text"
                    name="email"
                    className="input"
                    placeholder=" "
                    value={values.email}
                    onChange={handleChange}
                    onBlur={handleBlur}
                  />
                  <span className="placeholder">
                    <span className="prev_plh"></span>
                    <span className="text_plh">E-mail</span>
                    <span className="last_plh"></span>
                  </span>
                  <span className="error">{touched.email && errors.email}</span>
                </label>
                <label className="input_label">
                  <input
                    type="text"
                    name="telegram"
                    className="input"
                    placeholder=" "
                    value={values.telegram}
                    onChange={handleChange}
                    onBlur={handleBlur}
                  />
                  <span className="placeholder">
                    <span className="prev_plh"></span>
                    <span className="text_plh">Telegram</span>
                    <span className="last_plh"></span>
                  </span>
                  <span className="error">{touched.telegram && errors.telegram}</span>
                </label>
                <label className="input_label">
                  <textarea
                    name="comment"
                    className="input"
                    placeholder=" "
                    value={values.comment}
                    onChange={handleChange}
                    onBlur={handleBlur}
                  />
                  <div className="placeholder">
                    <span className="prev_plh"></span>
                    <span className="text_plh">Message</span>
                    <span className="last_plh"></span>
                    <div className="before"></div>
                  </div>
                  {touched.comment && <span className="error">{errors.comment}</span>}
                </label>
                <button className="all_btn" type="submit">
                  Send
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};
