import React, { useState, useEffect } from 'react';

import { getTime } from '../Config'

export default () => {

	const [ Time, setTime ] = useState(getTime());

	useEffect(() => {
		setTimeout(() => {
			setTime(getTime());
		}, 60000);
	})

	return (
		<React.Fragment>
        <div className="header_block">
			<div className="phone_info flex flex_m flex_sb">
				<div className="network flex flex_m flex_sb">
					<div className="antenna flex flex_b flex_sb">
						<div className="antenna_elem"></div>
						<div className="antenna_elem"></div>
						<div className="antenna_elem"></div>
						<div className="antenna_elem"></div>
					</div>
					<div className="connection">LTE</div>
				</div>
				<div className="special flex flex_m flex_sb">
					<img src="assets/img/battery.svg" alt="img" className="battery"/>
					<div className="time">{ Time }</div>
				</div>
			</div>
		</div>
		</React.Fragment>
    )
}