import React, {useState, useEffect} from 'react';
import {getTime, getDate} from '../Config'
import $ from 'jquery';
import Volume from './Volume';
import {copyText} from "../utils/copyText";
import {walletNum} from "../constants/walletNum";


export default (props) => {
    const [volume, setVolume] = useState(false);
    const [Time, setTime] = useState(getTime());

    useEffect(() => {
        setTimeout(() => {
            setTime(getTime());
        }, 60000);
    })

    return (
        <React.Fragment>
            <div className="start_panel flex flex_sb">
                <div className="flex">
                    <div className="start_open flex flex_c flex_m" onClick={() => props.start(!props.open)}>
                        <img src="assets/img/index/start.svg" alt=""/>
                    </div>
                    <div className="start_search flex flex_sb flex_m">
                        <div className="tooltip" id="copy_tooltip">
                            <span className="hovered_tooltip">Copied</span>
                        </div>
                        <p id="search_wallet">{walletNum}</p>
                        <img src="assets/img/index/copy.svg" id="search_copy" alt=""
                             onClick={() => copyText(walletNum)}/>
                    </div>
                    <div className="start_btn flex flex_c flex_m">
                        <img src="assets/img/index/e_white.svg" alt=""/>
                    </div>
                    <div className="start_btn flex flex_c flex_m">
                        <img src="assets/img/index/shop.svg" alt=""/>
                    </div>


                </div>

                <div className="flex flex_m">
                    <div className="start_btn flex flex_c flex_m hovered" onClick={() => setVolume(true)}>
                        <img src="assets/img/index/b_25.png" alt=""/>
                        <div className="tooltip">
                            <span className="hovered_tooltip">Volume: 0</span>
                        </div>
                    </div>
                    <div className="start_btn flex flex_c flex_m">
                        <p>ENG</p>
                    </div>
                    <div className="start_btn flex flex_c flex_m">
                        <img src="assets/img/index/ethernet.svg" alt=""/>
                    </div>
                    <div className="start_date right">
                        <p>{Time}</p>
                        <p>{getDate()}</p>
                    </div>
                </div>

            </div>

            {volume ? <Volume close={setVolume}/> : null}

        </React.Fragment>
    )
}