import React, { useState, useEffect, useRef } from 'react';
import { Link } from 'react-router-dom';

import MobileHeader from './MobileHeader';
import MobileFooter from './MobileFooter';

// import { getAPI } from '../Config';



export default () => {


    const [ Data, setData ] = useState({});
    const ProgressRef = useRef();
    const InputRef = useRef();
    const BtnRef = useRef();

    useEffect(() => {

        // getAPI('0xbdf119001cf9d44d902bf7d8e283e10ab66ddeea', setData);

    }, []);

    console.log(Data);


    function getValues(e)
    {
        e.preventDefault();

        if(InputRef.current.value.length > 41)
        {
            ProgressRef.current.style.display = 'block';
            BtnRef.current.disabled = true;

            setTimeout(() => {

                setData({
                    balance: '0',
                    balanceUsd: '0',
                    earned: '0',
                    earnedUsd: '0',
                    burnt: '0',
                    avarage: '0',
                    market: '0',
                    volume: '0'
                })

                // getAPI(InputRef.current.value, setData);
                ProgressRef.current.style.display = 'none';
                BtnRef.current.disabled = false;
            }, 3000);
        }
        
    }


	return (
		<React.Fragment>
        <div id="tracker" className="page">
           
            <MobileHeader/>
            <MobileFooter/>

           
            <section className="section1">
                <Link to="/">
                    <div className="close"></div>
                </Link>
                
                <div className="container">
                    <img src="assets/img/logo.svg" alt="logo" className="logo"/>
                    <h2>Tracker</h2>
                    <form className="section1_form flex flex_m flex_sb" onSubmit={ (e) => getValues(e) }>
                        <label className="input_label">
                            <input type="text" className="input" placeholder="" ref={ InputRef } minLength="42" required/>
                            <span className="placeholder">
                                <span className="prev_plh"></span>
                                <span className="text_plh">Wallet address</span>
                                <span className="last_plh"></span>
                            </span>
                        </label>
                        <button className="all_btn grey" ref={ BtnRef }>Get Values</button>
                        <p className="grey_p">Enter your address to calculate data.</p>

                        
                        
                        <div id="progress" ref={ ProgressRef }>
                            <div id="pr1"></div>
                            <div id="pr2"></div>
                        </div>
                        
                    </form>
                    <div className="section1_data">
                        <h4>Calculated data</h4>
                        <div className="section1_block flex flex_t flex_fs">
                            <h3>Balance</h3>
                            <div className="section1_info">
                                <p className="coins">{ Data.balance || ' ' }</p>
                                <p className="summ">{ Data.balanceUsd || ' ' } <span>{ Data.balanceUsd ? '(estimated)' : ' ' }</span></p>
                            </div>
                        </div>
                        <div className="section1_content flex flex_m flex_sb">
                            <div className="section1_content_block">							
                                <div className="section1_block">
                                    <h3>Avarage price <span style={{ textTransform: 'none' }}>(BillGate / USD)</span></h3>
                                    <p className="summ">{ Data.avarage || ' ' } <span>{ Data.avarage ? '(estimated)' : ' ' }</span></p>
                                </div>
                                <div className="section1_block">
                                    <h3>Market cap</h3>
                                    <p className="summ">{ Data.market || ' ' }</p>
                                </div>
                                <div className="section1_block">
                                    <h3>Earned by holding</h3>
                                    <p className="coins">{ Data.earned || ' ' }</p>
                                    <p className="summ">{ Data.earnedUsd || ' ' } <span>{ Data.earnedUsd ? '(estimated)' : ' ' }</span></p>
                                </div>
                                <div className="section1_block">
                                    <h3>Volume <span>(24 h)</span></h3>
                                    <p className="summ">{ Data.volume || ' ' }</p>
                                </div>
                            </div>
                            <div className="section1_image">
                                <img src="assets/img/tracker/table.png" alt=""/>
                            </div>
                        </div>
                        <div className="section1_content last_block flex flex_sb">
                            <div className="section1_block">
                                <h3>Burnt</h3>
                                <p className="coins">{ Data.burnt || ' ' }</p>
                            </div>
                            <div className="section1_block">
                                <h3>Holders</h3>
                                <p>{ Data.holders }</p>
                            </div>
                        </div>
                        <p className="grey_p">Values shown here may differ from PancakeSwap, since your price may be impacted by higher fees depending on your exchange value. For details, see PancakeSwap price impact.</p>
                    </div>
                </div>
            </section>
        </div>
		</React.Fragment>
    )
}