import React, { useState, useEffect } from 'react';
import { Redirect } from 'react-router-dom';
import $ from 'jquery'

import StartMenu from './StartMenu';
import Panel from './Panel';
import MobileDesktop from './MobileDesktop';
import CommingSoon from './CommingSoon';
import Burned from './Burned';
import Success from './Success';
import Contacts from './Contacts';

export default () => {

	const [openStart, setStart] = useState(true);

	const [ commSoon, setCommSoon ] = useState(false);
	const [ success, setSuccess ] = useState(false);
	const [ contacts, setContacts ] = useState(false);
	const [ sendContacts, setSendContacts ] = useState(false);
	const [ burned, setBurned ] = useState(false);

	
	const [ myComp, setMyComp ] = useState(false);

	useEffect(() => {
		$('.hovered').on('mousemove', function(e) {
		    //  
		    var pos = $(this).offset();
		    var elem_left = pos.left;
		    var elem_top = pos.top;
		    //    
		    var Xinner = e.pageX - elem_left;
		    var Yinner = e.pageY - elem_top;
		    var Tooltip = $(this).find('.tooltip');
		    var width = Tooltip.width();
		    var height = Tooltip.height();
		    var Left = Xinner - (width /2);
		    var Top = Yinner - height - 8;
		    Tooltip.show().css({'left': Left, 'top': Top});
		  });
		  $('.hovered').on('mouseout', function(e) {
		  	$('.tooltip').hide();
		  });
	}, [])


	return (
		<React.Fragment>
		<div id="desktop" className="page">
			<button className="main_btn mypc" onDoubleClick={ () => setMyComp(true) }>
				<img className="main_btn_img " src="assets/img/index/mypc.png" alt="img"/>
				<p>My computer</p>
			</button>
			{ myComp ? <Redirect to="/tracker" /> : null }
			
			<button className="main_btn rec hovered" onDoubleClick={ () => setBurned(true) }>
				<img className="main_btn_img" src="assets/img/index/rec.png" alt="img"/>
				<div className="tooltip">
					<span className="hovered_tooltip">Burned: 0</span>
				</div>
				<p>Recycle bin</p>
			</button>

			<div className="bottom_warning">
				<p className="big_warn">Activate Billdows</p>
				<p>BillGate has no affiliation with Bill Gates</p>
			</div>

			<StartMenu 
				open={ openStart }
				commsoon={ setCommSoon }
				contacts={ setContacts }
			/>
			<Panel start={ setStart } open={ openStart }/>

			<MobileDesktop/>

			{ commSoon ? <CommingSoon close={ setCommSoon }/> : null }
			{ burned ? <Burned close={ setBurned } /> : null }
			
			
			{ success ? <Success close={ setSuccess }/> : null }
			{ contacts ? <Contacts setContacts={ setContacts } setSuccess={setSuccess}/> : null }
		</div>

		</React.Fragment>
	)
}